@use 'base/mixins' as m;
@use 'base/variables' as v;

.table {
  width: 100%;
  border-collapse: collapse;
  &__thead {
    background-color: v.$gris-oscuro;
    color: v.$blanco;
  }
  &__th {
    padding: 1rem;
    text-align: left;
  }
  &__tr {
    &:nth-of-type(even) {
      background-color: v.$gris-claro;
    }
  }
  &__td {
    padding: 1.5rem 1rem;
    &--acciones {
      @extend .table__td;
      display: flex;
      justify-content: flex-end;
      gap: 2rem;
      align-items: center;
    }
  }
  &__accion {
    all: unset;
    text-transform: uppercase;
    font-size: 1.4rem;
    font-weight: v.$bold;
    &--editar {
      color: v.$primario;
    }
    &--eliminar {
      color: v.$rojo-claro;
    }
    &:hover {
      cursor: pointer;
    }
  }
}
