@use 'base/mixins' as m;
@use 'base/variables' as v;

.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: v.$gris-claro;
  &__header {
    background-color: v.$gris-oscuro;
    padding: 0.5rem;
    @include m.tablet {
      padding: 2rem;
    }
  }
  &__header-grid {
    display: flex;
    flex-direction: column;
    @include m.tablet {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  }
  &__logo--icon {
    width: 5rem;
    margin-top: -1rem;
    margin-right: 1rem;
  }
  &__logo {
    display: flex;
    @include m.logo;
    font-size: 2rem;
    @include m.tablet {
      @include m.texto-fluido(2rem, 3rem);
    }
  }
  &__nav {
  }
  &__form {
    display: flex;
    justify-content: center;
  }
  &__submit--logout {
    @include m.enlace;
  }
  &__grid {
    display: flex;
    flex: 1;
    overflow-y: auto;
  }
  &__sidebar {
    background-color: lighten(v.$gris-oscuro, 10);
    width: 7.5rem;
    @include m.tablet {
      width: 12rem;
    }
  }
  &__menu {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  &__enlace {
    color: lighten(v.$gris-oscuro, 70);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    transition: 300ms background-color ease-in-out, 300ms color ease,
      300ms border-radius ease;
    &:hover,
    &--actual {
      background-color: lighten(v.$gris-oscuro, 20);
      color: #fff;
      border-radius: 1rem;
    }
  }
  &__icono {
    font-size: 2rem;
  }
  &__menu-texto {
    font-size: 0.9rem;
    text-transform: uppercase;
    font-weight: 200;
    margin-top: 0.5rem;
    @include m.tablet {
      font-size: 1.1rem;
      font-weight: v.$bold;
    }
  }
  &__contenido {
    width: 100%;
    padding: 1rem 1rem 3rem 1rem;
    overflow-y: scroll;
    @include m.tablet {
      padding: 1rem 5rem 3rem 5rem;
    }
  }

  &__contenedor-boton {
    @include m.tablet {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 5rem;
    }
  }
  &__boton {
    @include m.boton(v.$azul, v.$blanco);
    text-transform: uppercase;
    font-size: 1.4rem;
    border-radius: 0.5rem;
    margin-bottom: 3rem;
    @include m.tablet {
      width: auto;
      margin-bottom: 0;
    }
  }

  &__contenedor {
    @include m.shadow;
    background-color: v.$blanco;
    padding: 2rem;
    border-radius: 1rem;
    overflow-x: scroll;
  }

  &__formulario {
    @extend .dashboard__contenedor;
    @include m.contenedor-md;
  }
  &__noData {
    width: 40rem;
    display: flex;
    margin: 0 auto;
  }
}
